.about{
    margin: 100px 0;
    &__top{
        h2{
            font-size: 30px;
            font-weight: 900;
            font-family: sans-serif;
            text-align: center;
            color:#fff;
        }
    }
    &__title{
        h2{
            font-size: clamp(0.75rem, 0.523rem + 1.14vw, 1.375rem);
            font-weight: 600;
            color: rgb(200, 200, 200);
            font-family: sans-serif;
            text-align: start;
            width: 100%;
            text-align: center;
            span{
                color: transparent;
            }
        }
    }
    &__wrapper{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 20px;
        width: 100%;
        margin: 0 auto;
    }
}